@import url('https://fonts.googleapis.com/css?family=Roboto');

@font-face {
  font-family: 'Fira Code';
  font-style: normal;
  font-weight: normal;
  src: url('../../fonts/FiraCode-Regular-webfont.woff') format('woff');
}

* {
  border: 0;
  margin: 0;
  outline: 0;
  padding: 0;
}

html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
}

.App {
  color: #313840;
  font: 18px/30px Roboto, Helvetica, Arial, sans-serif;
  /* font: 13px/21px 'Fira Code', Menlo, 'DejaVu Sans Mono', Monaco, 'Courier New', Courier, monospace; */
}

.SplashPage {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  font-family: 'Fira Code', Menlo, 'DejaVu Sans Mono', Monaco, 'Courier New', Courier, monospace;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
}

.SplashPage .Header {
  -ms-flex: 4 1;
      flex: 4 1;
}

.SplashPage .SplashInstallInfo {
  background: #e4e8ec;
  -ms-flex-positive: 0;
      flex-grow: 0;
  font-size: 2em;
}

.SplashPage .SplashNav {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 50px;
}



